*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Oswald', sans-serif;

}

body{
    background-color:rgb(211, 66, 9);
}

.App{
    display:flex;
    align-items:center;
    justify-content: center;
    min-height: 100vh;
}

.c-player {
    display: block;
    background-color: #313131;
    margin:0px auto;
    padding: 50px;
    border-radius: 16px;
    box-shadow: inset -6px -6px 12px rgba(0,0,0,0.8), inset 6px 6px 12px rgba(255,255,255,0.4);
}

.c-player > h4{
    color: rgb(211, 66, 9);
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 600;
    text-align: center;
    text-shadow: 1px 1px 1px black;
}

.c-player > p{
    color: #AAA;
    font-size: 14px;
    text-align: center;
    font-weight: 400;
}

.c-player--details .details-img {
    position: relative;
    width: fit-content;
    margin: 0 auto;
}

.c-player--details .details-img img{
    display: block;
    margin: 0px auto;
    width: 100%;
    max-width: 250px;
    border-radius: 50%;
    box-shadow: 6px 6px 12px rgba(0,0,0,0.8), -6px -6px 12px rgba(255,255,255,0.4);
}

.c-player--details .details-title {
    color: #EEE;
    font-size: 28px;
    text-shadow:2px 2px 4px rgba(0,0,0,0.8), -2px -2px 4px rgba(255,255,255,0.4); 
    text-align: center;
    margin-bottom: 10px;
}

.c-player--details .details-artist {
    color: #AAA;
    font-size: 20px;
    text-shadow:2px 2px 4px rgba(0,0,0,0.8), -2px -2px 4px rgba(255,255,255,0.4); 
    text-align: center;
    margin-bottom: 20px;
}

.c-player--controls {
    display:flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
}

.c-player--controls .play-btn {
    display: flex;
    margin: 0 30px;
    padding: 20px;
    border-radius: 50%;
    box-shadow: 
        4px 4px 10px rgba(0,0,0,0.8),
        -4px -4px 10px rgba(255,255,255,0.4),
        inset -4px -4px 10px rgba(0, 0, 0, 0.4),
        inset 4px 4px 10px rgba(255,255,255,0.4);
    border: none;
    outline: none;
    background-color:rgb(211, 66, 9);
    color: #FFF;
    font-size: 24px;
    cursor: pointer;
}

.c-player--controls .skip-btn{
    background: none;
    border: none;
    outline: none;
    cursor: pointer;
    color: #888;
    font-size: 18px;
}

